// Frontend Style
body.wp-admin .xx-styled{ max-width:100% !important; }

// Styled Area
.xx-styled{
	position:relative;
	padding: 5vw 0;
	font-size:1.25em;
	max-width:none;
	// Foreground, Background, Padding
	font-family:var(--foregroundCopyFont);
	color: var(--foregroundColor);
	background-image: var(--backgroundImage);
	background-color: var(--backgroundColor);
	background-size: var(--backgroundSize);
	background-position: var(--backgroundPosition);
	background-attachment: var(--backgroundAttachment);
	background-repeat: var(--backgroundRepeat);
	padding: var(--spacingMobile);
	@media(min-width: 992px){ padding: var(--spacingTablet); }
	@media(min-width:1200px){ padding: var(--spacingDesktop); }

	// Selection
	*::selection {
		color: var(--selectionFGColor);
		background-color: var(--selectionBGColor);
	}

	// Heading
	h1, h2, h3, h4, h5, h6{
		color: var(--headlineColor) !important; // This is to override WP
		font-family:var(--foregroundHeadlineFont);
	}
	
	p, h1, h2, h3, h4, h5, h6{
		max-width:800px;
		margin:1em auto;
	}

	// Dropcap
	p{
		&.has-drop-cap:not(:focus)::first-letter{
			color: var(--dropcapColor);
		}
	}

	// Link
	a{
		font-weight:bold;
		color: var(--linkColor);
	}

	// Caption	
	figure{
		figcaption{
			font-family:var(--foregroundCaptionFont);
		}
	}

}


